/*
  Default theme for BotUI
*/

$font-family: 'Open Sans', sans-serif;
$primary-color: #37BC9B;
$secondary-color: #209175;
$disabled-color: #bff8e9;

.botui_action {
  width: auto;
  max-width: 75%;
  padding: 7px 20px;
  display: inline-block;

  textarea,
  input:not([type='file']),
  select {
    border: 0;
    margin: 0 2px;
    border-radius: 0;
    padding: 5px 7px;
    vertical-align: bottom;
    font-family: $font-family;
    background-color: transparent;
    color: darken($primary-color, 22%);
    border-bottom: 1px solid $primary-color;
  }
}

.botui_button {
  border: 0;
  color: #fff;
  margin: 0 2px;
  line-height: 1;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  padding: 7px 15px;
  border-radius: 4px;
  vertical-align: bottom;
  font-family: $font-family;
  background: darken($primary-color, 10%);
  box-shadow: 2px 3px 4px 0 rgba(0,0,0,.25);

  &:hover,
  &:focus {
    box-shadow: 1px 2px 3px 0 rgba(0,0,0,.25);
  }
}

.botui_message_list {
  padding: 10px 20px 0;
}

.botui_action_container {
  padding: 10px 20px;
}

.botui_message_content {
  width: auto;
  max-width: 75%;
  overflow: scroll;
  padding: 7px 13px;
  border-radius: 15px;
  display: inline-block;
  color: darken($primary-color, 22%);
  background-color: lighten($primary-color, 35%);
  line-height: 1.2;

  &.human { // if human = true
    float: right;
    // color: lighten($primary-color, 40%);
    color:white;
    background-color: $primary-color;
  }

  iframe {
    border: 0;
  }
}

.botui_message_content::-webkit-scrollbar{
  display: none;
}

.botui_app_container {
  width: 100%; // mobile-first
  height: 100%;
  line-height: 1;

  @media (min-width: 400px) {
    height: 600px;
    margin: 0 auto;
    width: 600px;
    padding-right: 20px;
  }
}

.botui_container {
  width: 100%;
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
  // margin: 30px
}

.botui_container::-webkit-scrollbar{
  display: none;
}

.botui_message {
  margin: 10px 0;
  min-height: 20px;

  &:after {
    display: block;
    content: "";
    clear: both;
  }
}
.botui_message_container {
  width: 100%;
}

.botui_wait {
  line-height: 1.3;
}

/*
  Animation of loading dots
*/
.loading_dot {
  width: .5rem;
  height: .5rem;
  border-radius: .5rem;
  display: inline-block;
  background-color: $primary-color;

  &:nth-last-child(1) {
    margin-left: .3rem;
    animation: loading .6s .3s linear infinite;
  }
  &:nth-last-child(2) {
    margin-left: .3rem;
    animation: loading .6s .2s linear infinite;
  }
  &:nth-last-child(3) {
    animation: loading .6s .1s linear infinite;
  }
}

@keyframes loading {
  0% {
    transform: translate(0, 0);
    background-color: lighten($primary-color, 10%);
  }

  25% {
    transform: translate(0, -3px);
  }
  50% {
    transform: translate(0, 0px);
    background-color: lighten($primary-color, 10%);
  }
  75% {
    transform: translate(0, 3px);
  }
  100% {
    transform: translate(0, 0px);
  }
}

.slide-fade-enter-done {
  transition: all .3s ease;
}

.slide-fade-enter,
.slide-fade-exit-done {
  opacity: 0;
  transform: translateX(-10px);
}

.MessageInputBox > form > input {
  color: darken($primary-color, 18%);
  height: 1cm;
  width: 100%;
  border: solid $primary-color 1px;
  border-radius: 5px;
  // margin-left: 50px;
  // margin-top:20px;
  outline: none;
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  

  &:disabled{
    border: solid $disabled-color 1px;
  }

  &:focus{
    border: solid $secondary-color 2px;
  }

  &:hover {
    border: solid $secondary-color 2px;
  }

  &:active{
    border: solid $secondary-color 1px;
  }

  &::placeholder{
    color: lighten(#37BC9B, 15%);
  }

  @media(max-width : 400px){
    height: 0.5cm
  }
}

.MessageInputBox > form{
  width: 60%;
  display: flex;
  flex-direction: row;
  padding: 2px;
  justify-content: center;
  margin: auto;
}

.MessageInputBox > form > button{
  color: white;
  background-color: lighten($secondary-color,10%);
  border-radius: 5px;
  height: 1cm;
  width: 2cm;
  // margin-top: 20px;
  border: solid lighten($secondary-color,10%);
  margin-left: 10px;

  &:disabled{
    background-color: $disabled-color;
    border: solid $disabled-color;
  }

  &:hover{
    background-color: $secondary-color;
    border: solid $secondary-color;
  }

  @media(max-width : 400px){
    height: 0.5cm;
    width:1.25cm
  }
}

.MessageContainer{
  width: 100%;
}

.MainContainer{
  display: flex;
  flex-direction: column;
}

.disclaimer{
  color: grey;
  font-size:small;
  text-align: center;
  margin-top: 20px;
}

.HeaderBar{
  background-image: linear-gradient(to left, darken($primary-color, 5%), white);
  color: lighten($disabled-color, 20%);
  font-size:25px;
  text-align: right;
  width: 90%;
  border-radius: 20px;
  height: 40px;
  margin-left: 50px;
  margin-right: 30px;
  padding-right: 25px;
  font-family: Georgia;
  // font-style:italic;
  font-weight: 400;
  box-shadow: 6px 0 8px -4px lightgrey;
  line-height: 40px;
}

.App{
  display: flex;
  flex-direction: column;
}